import React, { useState } from "react";
import { Link } from "gatsby";

function PageTemplate(props) {
  const [isExpanded, setIsExpanded] = useState(false);

  const handleToggle = (e) => {
    e.preventDefault();
    setIsExpanded(!isExpanded);
  }

  return (
    <>
    <nav id="side-bar">
      <Link id="initials" to="/">
        MH
      </Link>

      <ul id="menu" className={`collapsed ${isExpanded ? "is-expanded" : ""}`}>
        <li>
          <Link to='/about'>
            about me
          </Link>
        </li>
        <li>
          <a href='https://drive.google.com/file/d/1JAX1C2G02xuYuxuripNENvbYk7sqhqEd/view?usp=sharing' target='_blank'>
            resume
          </a>
        </li>
        <li>
          <a href='https://twitter.com/matias_huapaya' target='_blank'>Twitter</a>
        </li>
        <li>
          <a href='https://www.linkedin.com/in/matias-huapaya-2b50a0129/' target='_blank'>LinkedIn</a>
        </li>
        <li>
          <a href='mailto: huapmar97@gmail.com' target='_blank'>Email</a>
        </li>
      </ul>
      <button className="hamburger" onClick={handleToggle} onKeyDown={handleToggle}>{isExpanded ? '✕' : '☰'}</button>
    </nav>

    <div id='page'>
      {props.children}
    </div>

    <footer>
      <p>
        designed + coded with ♥ by Matias Huapaya
      </p>
    </footer>
    </>
  );
}

export default PageTemplate
